/* Form */

form {
	margin: 0 0 _size(element-margin) 0;
}

label {
	color: _palette(fg-bold);
	display: block;
	font-size: 0.9em;
	font-weight: _font(weight-bold);
	margin: 0 0 (_size(element-margin) * 0.5) 0;
}

input[type="text"],
input[type="password"],
input[type="email"],
input[type="tel"],
input[type="search"],
input[type="url"],
select,
textarea {
	@include vendor('appearance', 'none');
	background: _palette(bg);
	border-radius: _size(border-radius);
	border: none;
	border: solid 1px _palette(border);
	color: inherit;
	display: block;
	outline: 0;
	padding: 0 1em;
	text-decoration: none;
	width: 100%;

	&:invalid {
		box-shadow: none;
	}

	&:focus {
		border-color: _palette(accent);
		box-shadow: 0 0 0 1px _palette(accent);
	}
}

select {
	background-image: svg-url("<svg xmlns='http://www.w3.org/2000/svg' width='40' height='40' preserveAspectRatio='none' viewBox='0 0 40 40'><path d='M9.4,12.3l10.4,10.4l10.4-10.4c0.2-0.2,0.5-0.4,0.9-0.4c0.3,0,0.6,0.1,0.9,0.4l3.3,3.3c0.2,0.2,0.4,0.5,0.4,0.9 c0,0.4-0.1,0.6-0.4,0.9L20.7,31.9c-0.2,0.2-0.5,0.4-0.9,0.4c-0.3,0-0.6-0.1-0.9-0.4L4.3,17.3c-0.2-0.2-0.4-0.5-0.4-0.9 c0-0.4,0.1-0.6,0.4-0.9l3.3-3.3c0.2-0.2,0.5-0.4,0.9-0.4S9.1,12.1,9.4,12.3z' fill='#{_palette(border)}' /></svg>");
	background-size: 1.25em;
	background-repeat: no-repeat;
	background-position: calc(100% - 1em) center;
	height: _size(element-height);
	padding-right: _size(element-height);
	text-overflow: ellipsis;

	option {
		color: _palette(fg-bold);
		background: _palette(bg);
	}

	&:focus {
		&::-ms-value {
			background-color: transparent;
		}
	}

	&::-ms-expand {
		display: none;
	}
}

input[type="text"],
input[type="password"],
input[type="email"],
input[type="tel"],
input[type="search"],
input[type="url"],
select {
	height: _size(element-height);
}

textarea {
	padding: 0.75em 1em;
}

input[type="checkbox"],
input[type="radio"], {
	@include vendor('appearance', 'none');
	display: block;
	float: left;
	margin-right: -2em;
	opacity: 0;
	width: 1em;
	z-index: -1;

	& + label {
		@include icon(false, solid);
		color: _palette(fg);
		cursor: pointer;
		display: inline-block;
		font-size: 1em;
		font-weight: _font(weight);
		padding-left: (_size(element-height) * 0.6) + 0.75em;
		padding-right: 0.75em;
		position: relative;

		&:before {
			background: _palette(bg);
			border-radius: _size(border-radius);
			border: solid 1px _palette(border);
			content: '';
			display: inline-block;
			font-size: 0.8em;
			height: (_size(element-height) * 0.75);
			left: 0;
			line-height: (_size(element-height) * 0.75);
			position: absolute;
			text-align: center;
			top: 0;
			width: (_size(element-height) * 0.75);
		}
	}

	&:checked + label {
		&:before {
			background: _palette(fg-bold);
			border-color: _palette(fg-bold);
			color: _palette(bg);
			content: '\f00c';
		}
	}

	&:focus + label {
		&:before {
			border-color: _palette(accent);
			box-shadow: 0 0 0 1px _palette(accent);
		}
	}
}

input[type="checkbox"] {
	& + label {
		&:before {
			border-radius: _size(border-radius);
		}
	}
}

input[type="radio"] {
	& + label {
		&:before {
			border-radius: 100%;
		}
	}
}

::-webkit-input-placeholder {
	color: _palette(fg-light) !important;
	opacity: 1.0;
}

:-moz-placeholder {
	color: _palette(fg-light) !important;
	opacity: 1.0;
}

::-moz-placeholder {
	color: _palette(fg-light) !important;
	opacity: 1.0;
}

:-ms-input-placeholder {
	color: _palette(fg-light) !important;
	opacity: 1.0;
}

/* Inverted */

.inverted label {
	color: _palette(fg-bold-inverted);
}

.inverted input[type="text"],
.inverted input[type="password"],
.inverted input[type="email"],
.inverted input[type="tel"],
.inverted input[type="search"],
.inverted input[type="url"],
.inverted select,
.inverted textarea {
	background: _palette(bg);
	border: solid 1px _palette(border);
	color: _palette(fg);

	&:focus {
		border-color: _palette(accent);
		box-shadow: 0 0 0 1px _palette(accent);
	}
}

.inverted select {
	option {
		color: _palette(fg-bold);
		background: _palette(bg);
	}
}

.inverted input[type="checkbox"],
.inverted input[type="radio"], {

	& + label {
		color: _palette(fg);

		&:before {
			background: _palette(bg);
			border: solid 1px _palette(border);
		}
	}

	&:checked + label {
		&:before {
			background: _palette(fg-bold);
			border-color: _palette(fg-bold);
			color: _palette(bg);
			content: '\f00c';
		}
	}

	&:focus + label {
		&:before {
			border-color: _palette(accent);
			box-shadow: 0 0 0 1px _palette(accent);
		}
	}
}

.inverted::-webkit-input-placeholder {
	color: _palette(fg-light) !important;
	opacity: 1.0;
}

.inverted:-moz-placeholder {
	color: _palette(fg-light) !important;
	opacity: 1.0;
}

.inverted::-moz-placeholder {
	color: _palette(fg-light) !important;
	opacity: 1.0;
}

:-ms-input-placeholder {
	color: _palette(fg-light) !important;
	opacity: 1.0;
}
